import React, { useState } from "react";
import { Card, Dropdown, Form } from "react-bootstrap";
import { BiEdit } from "react-icons/bi";
import BootstrapTable from "react-bootstrap-table-next";
import paginationFactory from "react-bootstrap-table2-paginator";
import { BiSearch } from "react-icons/bi";
import { CiLocationOn } from "react-icons/ci";
import "react-datepicker/dist/react-datepicker.css";
import { useDispatch, useSelector } from "react-redux";
import { useEffect } from "react";
import { getUserList } from "../../../app/features/userManagement/user.slice";
import FullScreenLoader from "../../../authorization/FullScreenLoader";
import DoctorModal from "./DoctorModal";

export default function DoctorRole({ setAddAdmin, addAdmin }) {
  const [searchValue, setSearchValue] = useState();
  const [genderValue, setGenderValue] = useState();
  const [statusId, setStatusId] = useState(null);
  const userList = useSelector((state) => state);
  const { isLoading } = useSelector((state) => state.userRole);
  const dispatch = useDispatch();

  const selectRow = {
    mode: "checkbox",
  };
  useEffect(() => {
    const finalData = {
      roleId: 2,
      search: searchValue ? searchValue : null,
      statusId:
        statusId === "true" ? true : statusId === "false" ? false : null,
      genderId:
        genderValue === "101" ? 101 : genderValue === "102" ? 102 : null,
    };
    dispatch(getUserList({ finalData }));
  }, [dispatch, searchValue, genderValue, statusId]);

  const Doctorcolumns = [
    {
      dataField: "userId",
      text: "ID",
      sort: true,
      headerStyle: { backgroundColor: "#F1F1F1" },
    },
    {
      dataField: "name",
      text: "Doctor Name",
      sort: true,
      headerStyle: { backgroundColor: "#F1F1F1", width: "17%" },
      formatter: (row, cell) => {
        return (
          <div className="d-flex align-items-center">
            <img
              src={
                cell?.imageUrl
                  ? cell?.imageUrl
                  : `https://ui-avatars.com/api/?name=${`${cell?.name}`}&background=000071&color=fff`
              }
              alt="doctor"
              className="me-2 dt-round-img"
            />
            <span style={{ lineHeight: "1.2" }}>
              <p className="m-0 table-bold-text">{cell?.name}</p>
              <p className="m-0 table-normal-text">{cell?.email}</p>
              <p className="m-0 table-normal-text" style={{ color: "#999999" }}>
                {cell?.phoneNumber}
              </p>
            </span>
          </div>
        );
      },
    },
    {
      dataField: "currentAddress",
      text: "Location",
      sort: true,
      headerStyle: { backgroundColor: "#F1F1F1" },
    },
    {
      dataField: "mcrn",
      text: "MCRN",
      sort: true,
      headerStyle: { backgroundColor: "#F1F1F1" },
    },
    {
      dataField: "genderName",
      text: "Gender",
      sort: true,
      headerStyle: { backgroundColor: "#F1F1F1" },
    },
    {
      dataField: "status",
      text: "Status",
      sort: false,
      headerStyle: { backgroundColor: "#F1F1F1" },
      formatter: (row, cell) => (
        <p
          className={`mb-0 text-center py-1 custom-width-class ${
            cell?.status === "InActive" ? "inactive-status" : "active-status"
          }`}
        >
          {cell?.status === "InActive" ? "Inactive" : "Active"}
        </p>
      ),
    },
    {
      dataField: "action",
      text: "Action",
      sort: false,
      formatter: (row, cell) => {
        return (
          <div className="d-flex justify-content-center">
            <BiEdit
              style={{ color: "#1A1A1A", cursor: "pointer" }}
              size={20}
              onClick={() => setAddAdmin({ data: cell, show: true })}
            />
          </div>
        );
      },
      headerStyle: { backgroundColor: "#F1F1F1" },
      headerAlign: "center",
    },
  ];

  const customTotal = (from, to, size) => (
    <span className="react-bootstrap-table-pagination-total">
      <b> {from} </b> to <b>{to}</b> out of <b>{size} entries</b>
    </span>
  );

  const paginateOptions = {
    paginationSize: 8,
    pageStartIndex: 1,
    alwaysShowAllBtns: true,
    withFirstAndLast: false,
    hideSizePerPage: true,
    showTotal: true,
    paginationTotalRenderer: customTotal,
    disablePageTitle: true,
    sizePerPageList: [
      {
        text: "10",
        value: 10,
      },
    ],
  };

  return (
    <>
      <Card className="user-details-card">
        <div className="px-4 pt-3 d-flex justify-content-between custom-row align-items-center table-header-border">
          <span className="d-flex align-self-center">
            <Form.Control
              onKeyDown={(e) => {
                if (e.key === "Enter") {
                  setSearchValue(e.target.value);
                }
              }}
              onChange={(e) => {
                if (e.key === "Enter") {
                  setSearchValue(e.target.value);
                }
              }}
              type="text"
              placeholder="Search"
              className="me-2 mb-3 search-field-spacing"
              aria-label="Search"
            />
            <BiSearch size={22} className="searchbar-icon" />
          </span>
          <div className="d-flex">

            <div className="mb-2 me-2">
              <select
                onChange={(e) => setGenderValue(e.target.value)}
                className="form-select pe-5"
                aria-label="Default select example"
              >
                <option selected>Gender</option>
                <option value={101}>Male</option>
                <option value={102}>Female</option>
              </select>
            </div>
            <div className="mb-2">
              <select
                onClick={(e) => setStatusId(e.target.value)}
                className="form-select pe-5"
                aria-label="Default select example"
              >
                <option selected>Status</option>
                <option value="true">Active</option>
                <option value="false">Inactive</option>
              </select>
            </div>
          </div>
        </div>
        {/* </> */}
        {/* {userList?.userRole?.isError === true && (
          <p className="text-center">Network Error...</p>
        )} */}
        {isLoading ? (
          <FullScreenLoader />
        ) : (
          <span className="doctor-datatable">
            <BootstrapTable
              columns={Doctorcolumns}
              data={
                userList?.userRole?.getAllUser?.data
                  ? userList?.userRole?.getAllUser?.data
                  : []
              }
              keyField="userId"
              id="bar"
              pagination={paginationFactory(paginateOptions)}
              // striped
              bordered={false}
              wrapperClasses="table-responsive"
              selectRow={selectRow}
              sort={{ dataField: "userId", order: "desc" }}
            />
          </span>
        )}
      </Card>
      <DoctorModal
        data={addAdmin.data}
        show={addAdmin.show}
        onClose={() => setAddAdmin({ show: false, data: null })}
      />
    </>
  );
}
