import React, { useState } from "react";
import { Card, Form } from "react-bootstrap";
import { BiEdit } from "react-icons/bi";
import BootstrapTable from "react-bootstrap-table-next";
import paginationFactory from "react-bootstrap-table2-paginator";
import { BiSearch } from "react-icons/bi";
import "react-datepicker/dist/react-datepicker.css";
import { useEffect } from "react";
import { useDispatch, useSelector } from "react-redux";
import { getUserList } from "../../../app/features/userManagement/user.slice";
import PatientModal from "./PatientModal";
import FullScreenLoader from "../../../authorization/FullScreenLoader";
import ReactDatePicker from "react-datepicker";
import { BsCalendarFill } from "react-icons/bs";
import moment from "moment";

export default function PatientRole({ setAddAdmin, addAdmin }) {
  const [searchValue, setSearchValue] = useState();
  const [selectedDate, setSelectedDate] = useState(null);
  const [genderValue, setGenderValue] = useState();
  const userList = useSelector((state) => state);
  const { isLoading } = useSelector((state) => state.userRole);

  const dispatch = useDispatch();

  const selectRow = {
    mode: "checkbox",
  };

  useEffect(() => {
    const finalData = {
      roleId: 3,
      search: searchValue,
      genderId:
        genderValue === "101" ? 101 : genderValue === "102" ? 102 : null,
      dob: selectedDate ? moment(selectedDate).format("YYYY-MM-DD") : null,
    };
    dispatch(getUserList({ finalData }));
  }, [dispatch, searchValue, genderValue, selectedDate]);

  const Patientcolumns = [
    {
      dataField: "userId",
      text: "ID",
      sort: true,
      headerStyle: { backgroundColor: "#F1F1F1" },
    },
    {
      dataField: "name",
      text: "Patient Name",
      sort: true,
      headerStyle: { backgroundColor: "#F1F1F1", width: "17%" },
      formatter: (row, cell) => {
        return (
          <div className="d-flex align-items-center">
            <img
              src={
                cell?.imageUrl
                  ? cell?.imageUrl
                  : `https://ui-avatars.com/api/?name=${`${cell?.name}`}&background=000071&color=fff`
              }
              alt="patient"
              className="me-2 dt-round-img"
            />
            <span style={{ lineHeight: "1.2" }}>
              <p className="m-0 table-bold-text">{cell?.name}</p>
              <p className="m-0 table-normal-text">{cell?.email}</p>
              <p className="m-0 table-normal-text" style={{ color: "#999999" }}>
                {cell?.phoneNumber}
              </p>
            </span>
          </div>
        );
      },
    },
    {
      dataField: "currentAddress",
      text: "Location",
      sort: true,
      headerStyle: { backgroundColor: "#F1F1F1" },
    },
    {
      dataField: "genderName",
      text: "Gender",
      sort: true,
      headerStyle: { backgroundColor: "#F1F1F1" },
    },
    {
      dataField: "dob",
      text: "DOB",
      sort: true,
      headerStyle: { backgroundColor: "#F1F1F1" },
      formatter: (cell) => {
        const date = new Date(cell);
        const day = date.getDate().toString().padStart(2, "0");
        const month = (date.getMonth() + 1).toString().padStart(2, "0");
        const year = date.getFullYear();
        return `${month}/${day}/${year}`;
      },
    },
    {
      dataField: "action",
      text: "Action",
      sort: false,
      formatter: (row, cell) => {
        return (
          <div className="d-flex justify-content-center">
            <BiEdit
              style={{ color: "#1A1A1A", cursor: "pointer" }}
              size={20}
              onClick={() => setAddAdmin({ data: cell, show: true })}
            />
          </div>
        );
      },
      headerStyle: { backgroundColor: "#F1F1F1" },
      headerAlign: "center",
    },
  ];
  const customTotal = (from, to, size) => (
    <span className="react-bootstrap-table-pagination-total">
      <b> {from} </b> to <b>{to}</b> out of <b>{size} entries</b>
    </span>
  );

  const paginateOptions = {
    paginationSize: 8,
    pageStartIndex: 1,
    alwaysShowAllBtns: true,
    withFirstAndLast: false,
    hideSizePerPage: true,
    showTotal: true,
    paginationTotalRenderer: customTotal,
    disablePageTitle: true,
    sizePerPageList: [
      {
        text: "10",
        value: 10,
      },
    ],
  };

  return (
    <>
      <Card className="user-details-card">
        <div className="ps-4 pt-3 d-flex justify-content-between custom-row align-items-center table-header-border">
          <div>
            <span className="d-flex align-self-center">
              <Form.Control
                onKeyDown={(e) => {
                  if (e.key === "Enter") {
                    setSearchValue(e.target.value);
                  }
                }}
                onChange={(e) => {
                  if (e.key === "Enter") {
                    setSearchValue(e.target.value);
                  }
                }}
                type="text"
                placeholder="Search"
                className="mb-3 search-field-spacing"
                aria-label="Search"
              />
              <BiSearch size={22} className="searchbar-icon" />
            </span>
          </div>

          <div className="mainDiv">

            <div className="mb-2">
              <select
                onChange={(e) => setGenderValue(e.target.value)}
                className="form-select pe-5"
                aria-label="Default select example"
              >
                <option selected>Gender</option>
                <option value={101}>Male</option>
                <option value={102}>Female</option>
              </select>
            </div>
            <ReactDatePicker
              selected={selectedDate}
              onChange={(date) => setSelectedDate(date)}
              dateFormat="dd MMM yyyy"
              placeholderText="DOB"
              className="custom-field-picker dr-date-w px-3"
            />
            <BsCalendarFill
              className="custom-date-icon"
              size={18}
              style={{ right: "42px" }}
            />
          </div>
        </div>
        {/* </> */}
        {/* {userList?.userRole?.isError === true && (
          <p className="text-center">Network Error...</p>
        )} */}
        {isLoading ? (
          <FullScreenLoader />
        ) : (
          <span className="doctor-datatable">
            <BootstrapTable
              columns={Patientcolumns}
              data={
                userList?.userRole?.getAllUser?.data
                  ? userList?.userRole?.getAllUser?.data
                  : []
              }
              keyField="userId"
              id="bar"
              pagination={paginationFactory(paginateOptions)}
              // striped
              bordered={false}
              wrapperClasses="table-responsive"
              selectRow={selectRow}
              sort={{ dataField: "userId", order: "desc" }}
            />
          </span>
        )}
      </Card>
      <PatientModal
        data={addAdmin.data}
        show={addAdmin.show}
        onClose={() => setAddAdmin({ show: false, data: null })}
      />
    </>
  );
}
