import React, { useEffect, useState } from "react";
import { Button, Col, Form, Modal, Row } from "react-bootstrap";
import { GENDER_TYPE } from "../../../components/LookupData/Data";
import PhoneInput from "react-phone-input-2";
import "react-phone-input-2/lib/style.css";
import { useDispatch, useSelector } from "react-redux";
import {
  createUser,
  getUserList,
  updateUser,
} from "../../../app/features/userManagement/user.slice";
import { Controller, useForm } from "react-hook-form";
import { AiOutlineEye, AiOutlineEyeInvisible } from "react-icons/ai";

export default function PatientModal({ show, onClose, data }) {
  const { register, handleSubmit, setValue, control, reset } = useForm();

  const [showPassword, setShowPassword] = useState(false);
  const handleClickShowPassword = () => setShowPassword((show) => !show);

  const { user } = useSelector((state) => state.auth);
  const dispatch = useDispatch();

  function onCreateSuccess() {
    let finalData = {
      userId: user.userId,
      roleId: 3,
    };
    dispatch(getUserList({ finalData }));
  }
  useEffect(() => {
    setValue("firstName", data?.firstName || "");
    setValue("lastName", data?.lastName || "");
    setValue("email", data?.email || "");
    setValue("password", data?.password || "");
    setValue("currentAddress", data?.currentAddress || "");
    setValue("dob", data?.dob || "");
    setValue("phoneNumber", data?.phoneNumber || "");
    setValue("genderId", data ? data?.genderId : "");
    // eslint-disable-next-line react-hooks/exhaustive-deps
  }, [data]);
  function createPatientRole(submittedData) {
    const finalData = {
      userId: data ? data?.userId : 0,
      roleId: 3,
      email: submittedData.email,
      phoneNumber: submittedData.phoneNumber,
      firstName: submittedData.firstName,
      lastName: submittedData.lastName,
      currentAddress: submittedData.currentAddress,
      genderId: submittedData?.genderId,
      dob: submittedData.dob,
      password: submittedData.password,
      staffRoleId: 5,
      image: null,
    };
    if (data) {
      dispatch(updateUser({ finalData, onCreateSuccess }));
    } else {
      dispatch(createUser({ finalData, onCreateSuccess }));
    }
    onClose();
    reset();
  }

  return (
    <>
      <Modal show={show} onHide={onClose} size="lg">
        <Form onSubmit={handleSubmit(createPatientRole)}>
          <Modal.Header closeButton>
            <Modal.Title style={{ fontSize: "19px", fontWeight: "bold" }}>
              {data ? "Edit" : "Add"} Patient
            </Modal.Title>
          </Modal.Header>
          <Modal.Body className="p-4">
            <Row>
              <Col lg={6}>
                <Form.Group className="mb-3" controlId="formFirstName">
                  <Form.Label className="fw-bold">First Name</Form.Label>
                  <Form.Control
                    type="text"
                    placeholder="John"
                    size="lg"
                    {...register("firstName", { required: !data })}
                    // defaultValue={data?.firstName}
                    maxLength={50}
                  />
                </Form.Group>
              </Col>
              <Col lg={6}>
                <Form.Group className="mb-3" controlId="formLastName">
                  <Form.Label className="fw-bold">Last Name</Form.Label>
                  <Form.Control
                    type="text"
                    placeholder="Smith"
                    size="lg"
                    {...register("lastName", { required: !data })}
                    // defaultValue={data?.lastName}
                    maxLength={50}
                  />
                </Form.Group>
              </Col>
              <Col lg={6}>
                <Form.Label className="fw-bold">Gender </Form.Label>
                <Form.Select
                  {...register("genderId", { required: !data })}
                  // defaultValue={data?.genderId}
                >
                  <option value="">Select Gender</option>
                  {GENDER_TYPE.map((type) => {
                    return <option value={type?.lookupId}>{type.value}</option>;
                  })}
                </Form.Select>
              </Col>
              <Col lg={6}>
                <Form.Group className="mb-3 position-relative" controlId="formPassword">
                  <Form.Label className="fw-bold">Password</Form.Label>
                  <Form.Control
                    type={showPassword ? "text" : "password"}
                    placeholder="Password"
                    size="lg"
                    {...register("password", { required: !data })}
                    // defaultValue={data?.lastName}
                    maxLength={50}
                  />
                  <div onClick={handleClickShowPassword} className="eye-icon text-cursor-pointer">
                      {showPassword ? (
                        <AiOutlineEye size={18} />
                      ) : (
                        <AiOutlineEyeInvisible size={18} />
                      )}
                    </div>
                </Form.Group>
              </Col>
              <Col lg={6}>
                <Form.Group className="mb-3" controlId="formDate">
                  <Form.Label className="fw-bold">Date </Form.Label>
                  <Form.Control
                    type="date"
                    placeholder="dd/mm/yy"
                    size="lg"
                    {...register("dob", { required: !data })}
                    // defaultValue={data?.dob}
                  />
                </Form.Group>
              </Col>

              <Col lg={6}>
                <Form.Group className="mb-3" controlId="formBasicEmail">
                  <Form.Label className="fw-bold">Email </Form.Label>
                  <Form.Control
                    type="email"
                    placeholder="Email"
                    size="lg"
                    {...register("email", { required: !data })}
                    // defaultValue={data?.email}
                    maxLength={50}
                  />
                </Form.Group>
              </Col>

              <Col lg={6}>
              <Form.Group className="mb-3" controlId="formNumber">
                  <Form.Label className="fw-bold">Phone Number</Form.Label>
                  <Controller
                    control={control}
                    name="phoneNumber"
                    rules={{ required: !data }}
                    defaultValue={data?.phoneNumber}
                    render={({ field }) => (
                      <PhoneInput
                        country="ie"
                        value={field.value}
                        onChange={(value) => field.onChange(`+${value}`)}
                      />
                    )}
                  />
                </Form.Group>
              </Col>

              <Col lg={6}>
                <Form.Group className="mb-3" controlId="formCurrentAddress">
                  <Form.Label className="fw-bold">Current Address </Form.Label>
                  <Form.Control
                    type="text"
                    placeholder="Type Address here"
                    size="lg"
                    {...register("currentAddress", { required: !data })}
                    // defaultValue={data?.currentAddress}
                    maxLength={150}
                  />
                </Form.Group>
              </Col>

              {/* <Col lg={12}>
                <Form.Group className="mb-3" controlId="formBasicEmail">
                  <Form.Label className="fw-bold">
                    Current Pharmacy Name and Address{" "}
                  </Form.Label>
                  <Form.Control
                    type="text"
                    placeholder="Pharmacy Name and Address"
                    size="lg"
                    {...register("pharmacyName", { required: !data })}
                    // defaultValue={data?.pharmacyName}
                    maxLength={150}
                  />
                </Form.Group>
              </Col> */}
            </Row>
          </Modal.Body>
          <Modal.Footer className="Doctor-Modal-Footer">
            <Button onClick={onClose} className="Admin-Modal-CancelBtn fw-bold">
              Cancel
            </Button>
            <Button
              variant="primary"
              type="submit"
              className="Admin-Add-btn fw-bold"
            >
              {data ? "Update" : "Add"} Patient
            </Button>
          </Modal.Footer>
        </Form>
      </Modal>
    </>
  );
}
